import { useReducer } from 'react'

const changeArrayObject = (_items, { event, index, key }) => {
  const items = [..._items]
  items.splice(index, 1, {
    ...items[index],
    [key]: event.target.value
  })
  return items
}

const shiftArrayObject = (_items, { index, shift }) => {
  const items = [..._items]
  const item = items.splice(index, 1)
  items.splice(index + shift, 0, item[0])
  return items
}

const removeArrayObject = (_items, { index }) => {
  const items = [..._items]
  items.splice(index, 1)
  return items
}

const onChangeColour = (colours, { key, value }) => {
  if (value) {
    return {
      ...colours,
      [key]: value
    }
  }
  return colours
}

const reducer = (_state, action) => {
  const state = JSON.parse(JSON.stringify(_state))

  switch(action.type) {
    case 'setDefault':
      const defaultPayload = JSON.parse(JSON.stringify(action.payload))
      return { ...state, default: defaultPayload, current: defaultPayload }
    case 'onColumnChange':
      state.current.tableHead = changeArrayObject(state.current.tableHead, action.payload)
      return state
    case 'onColumnShift':
      state.current.tableHead = shiftArrayObject(state.current.tableHead, action.payload)
      return state
    case 'onColumnAdd':
      state.current.tableHead.push({ title: '', key: '', fontSize: 'medium', align: 'left' })
      return state
    case 'onColumnRemove':
      state.current.tableHead = removeArrayObject(state.current.tableHead, action.payload)
      return state
    case 'onChangeColour':
      state.current.colour = onChangeColour(state.current.colour, action.payload)
      return state
    case 'onResetColour':
      state.current.colour[action.payload.key] = state.default.colour[action.payload.key]
      return state
    case 'onChangeMisc':
      state.current[action.payload.key] = action.payload.event.target.value
      return state
    case 'onResetMisc':
      state.current[action.payload.key] = state.default[action.payload.key]
      return state
    case 'onChangeCarouselMisc':
      state.current.carousel[action.payload.key] = action.payload.event.target.value
      return state
    case 'onChangeCarouselImage':
      state.current.carousel.images = changeArrayObject(state.current.carousel.images, action.payload)
      return state
    case 'onAddCarouselImage':
      state.current.carousel.images.push({ src: '' })
      return state
    case 'onShiftCarouselImage':
      state.current.carousel.images = shiftArrayObject(state.current.carousel.images, action.payload)
      return state
    case 'onRemoveCarouselImage':
      state.current.carousel.images = removeArrayObject(state.current.carousel.images, action.payload)
      return state
    case 'onChangeWeather':
      state.current.weather[action.payload.key] = action.payload.event.target.value
      return state
    case 'onResetWeather':
      state.current.weather[action.payload.key] = state.default.weather[action.payload.key]
      return state
    default:
      return state
  }
}

const useConfigReducer = () => {
  const initialState = {
    default: {},
    current: {},
  };
  const [state, dispatch] = useReducer(reducer, initialState);

  const dispatcher = {
    setDefault: (defaultConfig) => dispatch({
      type: 'setDefault', 
      payload: defaultConfig
    }),

    onColumnChange: (event, index, key) => dispatch({
      type: 'onColumnChange',
      payload: { event, index, key }
    }),

    onColumnShift: (index, shift) => dispatch({
      type: 'onColumnShift',
      payload: { index, shift }
    }),

    onColumnAdd: () => dispatch({ 
      type: 'onColumnAdd' 
    }),

    onColumnRemove: (index) => dispatch({
      type: 'onColumnRemove',
      payload: { index }
    }),

    onChangeColour: (key, value) => dispatch({
      type: 'onChangeColour',
      payload: { key, value }
    }),

    onResetColour: (key) => dispatch({
      type: 'onResetColour',
      payload: { key }
    }),

    onChangeMisc: (event, key) => dispatch({
      type: 'onChangeMisc',
      payload: { event, key }
    }),

    onResetMisc: (key) => dispatch({
      type: 'onResetMisc',
      payload: { key }
    }),

    onChangeCarouselMisc: (event, key) => dispatch({
      type: 'onChangeCarouselMisc',
      payload: { event, key }
    }),

    onChangeCarouselImage: (event, index, key) => dispatch({
      type: 'onChangeCarouselImage',
      payload: { event, index, key }
    }),

    onAddCarouselImage: () => dispatch({ 
      type: 'onAddCarouselImage'
    }),

    onShiftCarouselImage: (index, shift) => dispatch({
      type: 'onShiftCarouselImage',
      payload: { index, shift }
    }),

    onRemoveCarouselImage: (index) => dispatch({
      type: 'onRemoveCarouselImage',
      payload: { index }
    }),

    onChangeWeather: (event, key) => dispatch({
      type: 'onChangeWeather',
      payload: { event, key }
    }),

    onResetWeather: (key) => dispatch({
      type: 'onResetWeather',
      payload: { key }
    })
  }

  return [state.current, dispatcher]
}

export default useConfigReducer