import { useState, useEffect } from 'react'
import {
  Paper,
  TableContainer,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  TableFooter,
  Grid,
} from '@material-ui/core'
import Carousel from 'react-material-ui-carousel'
import styled from 'styled-components'

import WeatherWidget from './WeatherWidget'
import TokenModal from './TokenModal'
import useConfig from '../../hooks/useConfig'
import useCatalog from '../../hooks/useCatalog'
import useTokenStorage from '../../hooks/useTokenStorage'

const Container = styled.div`
  background-color: ${props => props.colour};
  min-height: 100vh;
  padding: 1rem;
`
const Image = styled.img`
  display: block;
  margin: auto;
  max-height: 10rem;
`
const CarouselImage = styled.img`
  display: block;
  margin: 0.5rem auto;
  height: 30rem;
`
const FlagImageCell = styled.img`
  box-shadow: 0px 0px 3px 1px #a2a2a2;
  width: ${props => {
    switch(props.size) {
      case 'small':
        return '2.5rem';
      case 'medium':
        return '5rem';
      case 'large':
        return '7.5rem';
      default:
        return '5rem';
    }
  }};
`
const FontCell = styled.div`
  font-size: ${props => {
    switch(props.size) {
      case 'small':
        return 'large';
      case 'medium':
        return 'x-large';
      case 'large':
        return 'xx-large';
      default:
        return 'x-large';
    }
  }};
`
const PaperContainer = styled(Paper)`
  overflow: auto;
  height: 100%;

  th {
    font-weight: bold;
  }
`
const ColouredTableCell = styled(TableCell)`
  color: ${props => props.fontColour} !important;
  background-color: ${props => props.colour};
  border-bottom-color: ${props => props.borderColour} !important;
`

const cellDisplay = (column, row) => {
  switch(column.key) {
    case 'flag':
      return <FlagImageCell
        size={column.size}
        src={row[column.key]}
        alt={row[column.key]}
      />
    default:
      return <FontCell size={column.size}>{row[column.key]}</FontCell>
  }
}

const ImageCarousel = ({ items, interval }) => {
  return (
    <Carousel
      stopAutoPlayOnHover={false}
      swipe={false}
      indicators={false}
      navButtonsAlwaysInvisible={true}
      interval={Number(interval) * 1000}
    >
      {items.map(item => <CarouselImage src={item.src} />)}
    </Carousel>
  )
}

const RateBoard = ({ config }) => {
  const [token, setToken] = useTokenStorage()
  const jsonConfig = useConfig(token);
  const catalog = useCatalog();
  const [allRates, setAllRates] = useState([])

  config = config || jsonConfig;

  useEffect(() => {
    if (config.isAllRates) {
      setAllRates(catalog.rates || [])
    } else {
      setAllRates((catalog.rates || []).filter(rate => rate.isFlagged))
    }
  }, [catalog.rates?.length])

  useEffect(() => {
    setTimeout(() => {
      const rates = [...allRates]
      if (rates.length > 0) {
        const firstRate = rates.shift();
        setAllRates([...rates, firstRate])
      }
    }, Number(config.rowsInterval) * 1000)
  }, [allRates, config.rowsInterval])

  const rates = allRates.slice(0, Number(config.maxRows));

  const tableHead = config.tableHead || [];
  const colour = config.colour || {};
  const carousel = config.carousel || {};
  const weather = config.weather || {};

  const timestamp = <FontCell>{catalog.timestamp}</FontCell> || '';

  return (
    <>
    <TokenModal open={!token} onSubmit={token => setToken(token)} />

    {token && (
      <Container colour={colour.background}>
        <Image src={config.logo} alt="logo" />

        {carousel.position === 'top' && (
          <ImageCarousel items={carousel.images} interval={Number(carousel.interval)} />
        )}

        <Grid container spacing={2}>
          {weather.position === 'left' && (
            <Grid item xs={2}>
              <PaperContainer style={{ 
                backgroundColor: colour.weatherBackground, 
                color: colour.weatherFont
              }}>
                <WeatherWidget apiKey={weather.apiKey} units={weather.units} interval={weather.interval} />
              </PaperContainer>
            </Grid>
          )}

          <Grid item xs={weather.position === 'none' ? 12 : 10}>
            <PaperContainer elevation={3}>
              <TableContainer>
                <Table>
                  <TableHead>
                    {config.datePosition && config.datePosition.includes('top') && (
                      <TableRow>
                        <ColouredTableCell 
                          align={config.datePosition.split('-')[1]}
                          colSpan={tableHead.length}
                          colour={colour.header}
                          borderColour={colour.divider}
                          fontColour={'rgba(0, 0, 0, 0.54)'}
                        >
                          {timestamp}
                        </ColouredTableCell>
                      </TableRow>
                    )}
                    <TableRow>
                      {tableHead.map((col, i) => (
                        <ColouredTableCell 
                          key={col.key} 
                          align={col.align} 
                          colour={colour.header}
                          borderColour={colour.divider}
                          fontColour={colour.font}
                          style={{ width: col.width }}
                        >
                          <FontCell>{col.title}</FontCell>
                        </ColouredTableCell>
                      ))}
                    </TableRow>
                  </TableHead>

                  <TableBody>
                    {rates.map((row, i) => (
                      <TableRow key={row.iso}>
                        {tableHead.map(col => (
                          <ColouredTableCell 
                            key={`${row.iso}-${col.key}`}
                            align={col.align}
                            colour={!!(i%2) ? colour.odd : colour.even}
                            borderColour={colour.divider}
                            fontColour={colour.font}
                          >
                            {cellDisplay(col, row)}
                          </ColouredTableCell>
                        ))}
                      </TableRow>
                    ))}
                  </TableBody>

                  {config.datePosition && config.datePosition.includes('bottom') && (
                    <TableFooter>
                      <TableRow>
                        <ColouredTableCell 
                          align={config.datePosition.split('-')[1]}
                          colSpan={tableHead.length}
                          colour={colour.footer}
                          borderColour={colour.footer}
                          fontColour={colour.font}
                        >
                          {timestamp}
                        </ColouredTableCell>
                      </TableRow>
                    </TableFooter>
                  )}
                </Table>
              </TableContainer>
            </PaperContainer>
          </Grid>

          {weather.position === 'right' && (
            <Grid item xs={2}>
              <PaperContainer style={{ 
                backgroundColor: colour.weatherBackground, 
                color: colour.weatherFont
              }}>
                <WeatherWidget apiKey={weather.apiKey} units={weather.units} interval={weather.interval} />
              </PaperContainer>
            </Grid>
          )}
        </Grid>

        {carousel.position === 'bottom' && (
          <ImageCarousel items={carousel.images} interval={carousel.interval} />
        )}
      </Container>
    )}
    </>
  );
}

export default RateBoard;
