import { useState } from 'react'
import {
  Paper,
  Grid,
  Modal as BaseModal,
  Fade as BaseFade,
  TextField,
  Button
} from '@material-ui/core'
import styled from 'styled-components'

const Modal = styled(BaseModal)`
  display: flex;
  align-items: center;
  justify-content: center;
`
const Fade = styled(BaseFade)`
  :focus-visible {
    outline: none;
  }
`
const ModalPaper = styled(Paper)`
  padding: 1rem;
`

const TokenModal = ({ open, onSubmit }) => {
  const [token, setToken] = useState('')

  const onFormSubmit = (event) => {
    event.preventDefault()
    onSubmit(token)
  }

  return (
    <Modal
      open={open}
      closeAfterTransition
    >
      <Fade in={open}>
        <ModalPaper elevation={3}>
          <form onSubmit={onFormSubmit}>
            <Grid
              container
              direction="column"
              justify="center"
              alignItems="center"
              spacing={2}
            >
              <Grid item>
                <TextField 
                  variant="outlined" 
                  label="Token"
                  value={token}
                  onChange={event => setToken(event.target.value)}
                />
              </Grid>
              <Grid item><Button type="submit">Submit</Button></Grid>
            </Grid>
          </form>
        </ModalPaper>
      </Fade>
    </Modal>
  )
}

export default TokenModal